.select__checkbox-mark {
  position: relative;
  display: block;
  box-sizing: border-box;
  width: 24px;
  height: 24px;
  flex: none;
  border-radius: 5px;
  border: 2px solid #0f805d;
}
.select__input[type="checkbox"]:checked + .select__checkbox-mark::before {
  content: "";
  position: absolute;
  width: 5.5px;
  height: 2px;
  transform: rotate(45deg);
  top: 9px;
  left: 2.5px;
  background-color: #0f805d;
}
.select__input[type="checkbox"]:checked + .select__checkbox-mark::after {
  content: "";
  position: absolute;
  top: 7.5px;
  right: 2px;
  transform: rotate(-45deg);
  width: 9px;
  height: 2px;
  background-color: #0f805d;
}
