  display: flex;
  align-items: center;
  column-gap: 12px;
  font-size: 16px;
}
.select__label--disabled .select__checkbox-mark {
  border: 2px solid #8e9892;
}
.select__label:hover:not(.select__label--disabled) {
  color: #7bdb0a;
}
.select__label:hover:not(.select__label--disabled) .select__checkbox-mark {
  border: 2px solid #7bdb0a;
}
.select__label:hover:not(.select__label--disabled) .select__input[type="checkbox"]:checked + .select__checkbox-mark::before {
  background-color: #7bdb0a;
}
.select__label:hover:not(.select__label--disabled) .select__input[type="checkbox"]:checked + .select__checkbox-mark::after {
  background-color: #7bdb0a;
}
